import React from "react";
import { useEffect } from "react";
import * as THREE from "three";
import { BufferGeometry, Camera, Color, Vector2, Vector3 } from "three";
import * as BufferGeometryUtils from "../Common/BufferGeometryUtils";
import { genPolygon, Grid } from "./GenVertices";
import { calcHexGrid } from "../Common/APIHelper";
import { render } from "@testing-library/react";
import OxygenFontData from "@compai/font-oxygen-mono/data/typefaces/oxygen-mono-normal-400.json";
import { FontLoader } from "../Common/FontLoader";
import { TextGeometry } from "../Common/TextGeometry";
import {OBJLoader} from "three/examples/jsm/loaders/OBJLoader";
import CrownObj from '../assets/crown.obj';
import './Graph.scss';
import { inverseLerp, lerp } from "three/src/math/MathUtils";


interface cell {
  hue:number;
  lightness:number;
  position:THREE.Vector3;
  dist:number;
  height?:number;
}

interface animationProps {
  rad:number;
  rot:number;
  spinSpeed:number;
  drag: number;
}

interface IProps{}

interface IState {
  hueText:string;
}


export class Graph extends React.Component<IProps, IState>{
  private offset = new Vector3();
  private animationValues:animationProps = {
    rad: 0,
    rot: 0,
    spinSpeed: 0,
    drag: 0.05,
  };

  mount?:HTMLDivElement;

  camera:THREE.PerspectiveCamera;
  renderer:THREE.WebGLRenderer;
  scene:THREE.Scene;

  private prevMousePos?:THREE.Vector2;
  private touchDown:boolean;
  
  constructor(props:IProps) {
    super(props);
    this.state = {
      hueText:"",
    }
    this.touchDown = false;

    this.scene = new THREE.Scene();
    this.camera = new THREE.PerspectiveCamera( 75, 1, 0.1, 1000 );
    this.renderer = new THREE.WebGLRenderer({alpha: true});

    this.camera.position.set(0, 150, 120);
    this.camera.lookAt(new THREE.Vector3(0,0,0));

    this.animationValues.rad = Math.sqrt(Math.pow(this.camera.position.x, 2) + Math.pow(this.camera.position.z,2));
    this.animationValues.rot = Math.PI/2;
  }

  componentDidMount = () => {
    if (this.mount === undefined) {
      console.log("An unexpected error has occurred");
      return;
    }
    
    this.updateScreenSize();
    this.mount.appendChild(this.renderer.domElement );

    this.makeGraph();
  }

  private updateScreenSize = () => {    
    if (this.mount === undefined) {
      console.log("Ensure mount has been set");
      return;
    }

    requestAnimationFrame(this.updateScreenSize);
    let renderSize = new Vector2();
    this.renderer.getSize(renderSize);
    if (renderSize.x != this.mount.offsetWidth || renderSize.y != this.mount.offsetHeight) {
      this.renderer.setSize( this.mount.offsetWidth, this.mount.offsetHeight );
      const aspect = (this.mount.offsetWidth / this.mount.offsetHeight);
      this.camera.aspect = aspect;
      
      if (aspect < 0.85) {
        const lerpPerc = inverseLerp(0.85, 0, aspect);
        this.animationValues.rad = lerp(120, 300, lerpPerc);
        this.camera.position.y = lerp(150, 375, lerpPerc); 
        this.repositionCamera();
      }

      this.camera.updateProjectionMatrix();
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.render(this.scene, this.camera!);
    }
  }

  private genHexGrid = ():cell[] => {
    const minHue:number = 180 / 360;
    const maxHue:number = 300 / 360;
  
    const minLightness:number = 0;
    const maxLightness:number = 1;
  
    const graphThickness:number = 25;
    const holeRad:number = 0;
  
    const sideLength = Math.sin(Math.PI / 3);
  
    const cells:cell[] = [];
    for (let i = -30; i < 30; i++) {
      for (let j = -1; j > -30; j--) {
  
        const x = i * ( 2 + sideLength) + (j % 2) * 1.5;
        const y =  j * (sideLength);
        const dist = Math.sqrt(x * x + y * y);
        if (dist <= graphThickness + holeRad && dist >= holeRad) {
          cells.push({
            position:new THREE.Vector3(x, 0, y),
            hue: lerp(minHue, maxHue, Math.atan(x / y) / (Math.PI) + 1 / 2),
            lightness: maxLightness - (dist-holeRad) / graphThickness * (maxLightness - minLightness),
            dist: dist,
          });
        }
      }
    }
  
    return cells;
  }

  private makeGraph = () => {
    const cells:cell[] = this.genHexGrid();
    this.genHexMesh(cells).then(
      (geometry) => {
        console.log("HexMesh Generated");
        var material = new THREE.ShaderMaterial({
          uniforms: {
            bboxMin: {
              value: geometry.boundingBox!.min
            },
            bboxMax: {
              value: geometry.boundingBox!.max
            }
          },
          vertexShader: `
            uniform vec3 bboxMin;
            uniform vec3 bboxMax;
          
            varying vec2 vUv;
        
            void main() {
              vUv = uv;
              gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1.0);
            }
          `,
          fragmentShader: `
            uniform vec3 color1;
            uniform vec3 color2;
          
            varying vec2 vUv;
      
            vec3 HUEtoRGB(in float hue)
            {
                // Hue [0..1] to RGB [0..1]
                // See http://www.chilliant.com/rgb2hsv.html
                vec3 rgb = abs(hue * 6. - vec3(3, 2, 4)) * vec3(1, -1, -1) + vec3(-1, 2, 2);
                return clamp(rgb, 0., 1.);
            }
            
            vec3 HSLtoRGB(in vec3 hsl)
            {
                // Hue-Saturation-Lightness [0..1] to RGB [0..1]
                vec3 rgb = HUEtoRGB(hsl.x);
                float c = (1. - abs(2. * hsl.z - 1.)) * hsl.y;
                return (rgb - 0.5) * c + hsl.z;
            }
      
            void main() {
              
              gl_FragColor = vec4(HSLtoRGB(vec3(vUv.x, 1, vUv.y)), 1.0);
            }
          `,
        });
      
        var graph = new THREE.Mesh( geometry, material );
        this.scene.add(graph);

        this.markHeighest(cells);
          

        this.renderer.render( this.scene, this.camera );
  
        let rot = Math.PI / 2;
        let rad = Math.sqrt(Math.pow(this.camera.position.x, 2) + Math.pow(this.camera.position.z, 2));
      
        var raycaster = new THREE.Raycaster();

        window.addEventListener('pointerdown', (e) => {
          this.touchDown = true;
          raycastGraph(new Vector2(e.x, e.y));
        });
        window.addEventListener('pointermove', (e) => {
          spinGraph(new Vector2(e.x, e.y));
        });
        window.addEventListener('pointerup', (e) => {
          this.prevMousePos = undefined;
          this.touchDown = false;
        });
        window.addEventListener('touchstart', (e) => {
          this.touchDown = true;
          const touch = e.changedTouches[0];
          raycastGraph(new Vector2(touch.clientX, touch.clientY));
        })
        window.addEventListener('touchmove', (e) => {
          const touch = e.changedTouches[0];
          spinGraph(new Vector2(touch.clientX, touch.clientY));
        })
        window.addEventListener('touchend', (e) => {
          this.prevMousePos = undefined;
          this.touchDown = false;
        })

        const spinGraph = (pos:Vector2) => {
          if (this.touchDown) {
            const mousePos = this.pointerWithinCanvas(pos);
            if (this.prevMousePos === undefined) {
              this.prevMousePos = mousePos;
              return;
            }
    
            const delta = mousePos.x - this.prevMousePos.x;
            this.prevMousePos.setX(mousePos.x);
            
            const prevSpeed = this.animationValues.spinSpeed;
            if (Math.abs(delta) > Math.abs(prevSpeed)) {
              this.animationValues.spinSpeed = delta;
    
              if (Math.abs(prevSpeed) < 0.0005) {
                this.animate();
              }
            }
          }
        }

        const raycastGraph = (pos:Vector2) => {
          const mousePos = this.pointerWithinCanvas(pos);
          raycaster.setFromCamera(
            mousePos,
            this.camera,
          )
          const obj = raycaster.intersectObject(graph);
          if (obj.length > 0) {
            this.setState((prev, props) => ({
              hueText: `HSL(${Math.round(obj[0].uv!.x * 360)}, 100, ${Math.round(obj[0].uv!.y * 100)})`,
            }));
          }
        }
      }
    );
  }

  private pointerWithinCanvas = (e:Vector2) => {
    const rect:DOMRect = this.renderer!.domElement.getBoundingClientRect();
    const x = (e.x - rect.x) / rect.width * 2 - 1;
    const y = (e.y - rect.y) / rect.height * - 2 + 1;
    return new Vector2(x,y);
  }

  private markHeighest = (cells:cell[]) => {
    const ROTATION_SPEED = 0.03;
    const BOB_SPEED = 0.05;

    let maxHeight = 0;
    let position:Vector3;

    cells.forEach(cell => {
      if (cell.height !== undefined && cell.height > maxHeight) {
        position = cell.position;
        maxHeight = cell.height;
      }
    });
    
    const loader = new OBJLoader();
    loader.load(CrownObj,
      o=>{
        let crown:THREE.Group=o;
        crown.traverse(o => {
          if (o instanceof THREE.Mesh) {
            o.material = new THREE.MeshBasicMaterial({color: 0xffff00});
          }
        })
        crown.position.set(
          position.x - this.offset.x,
          maxHeight + 3,
          position.z - this.offset.z
        );
        this.scene!.add(crown);
        
        let time:number = 0;
        const animateCrown = () => {
          requestAnimationFrame(animateCrown);

          crown.position.setY(maxHeight + 3 + 2 * Math.sin(time));
          time += BOB_SPEED;

          crown.rotateY(ROTATION_SPEED);

          this.renderer!.render(this.scene!,this.camera!);
        }
        animateCrown();
      },
      o=>{},
      o=>{console.log(o)}
      );

  }

  private genHexMesh = (cells:cell[]):Promise<THREE.BufferGeometry> => {
    const maxHeight = 70;
    const hexRad = 4;
    const hexes:THREE.BufferGeometry[] = [];
  
    return new Promise<THREE.BufferGeometry>((resolve, reject) => {
      calcHexGrid({
      hues: cells.map(o=>o.hue),
      lightnesses: cells.map(o=>o.lightness),
      hueDist: 20,
      lightnessDist: 15,
      }).then(
        (heights) => {
          let max = 0;
          heights.forEach(element => {
            if (element > max) max = element
          });
          for (let i = 0; i < cells.length; i++) {
            const height = heights[i] / max * maxHeight;
            
            cells[i].height = height;

            let hex:THREE.BufferGeometry = genPolygon({sides: 6, top: "edge", radius: hexRad, height: height});
            const dx = cells[i].position.x * hexRad;
            const dy = cells[i].position.z * hexRad;
  
            /*if (heights[i] == max) {
              let geom:THREE.BufferGeometry = genPolygon({sides: 4, top: "edge", radius: 1.414, height: 2});
              let redUVs = []
              for (let i = 0; i < 9; i++) {
                redUVs.push(0, 0.5);
              }
              geom.setAttribute('uv', new THREE.BufferAttribute(new Float32Array(redUVs), 2));
              geom.translate(dx, height + 2, dy);
              hexes.push(geom);
            }*/
  
            const dist = Math.sqrt(dx * dx + dy * dy)
            hex.translate(dx,height,dy);
            const uvs:number[] = [];
            const uv2s:number[] = [];
            for (let j = 0; j < 13; j++) {
              uvs.push(cells[i].hue, cells[i].lightness);
              uv2s.push(i, height);
            }
            hex.setAttribute(
              'uv', 
              new THREE.BufferAttribute(
                new Float32Array(uvs),
                2
              )
            );
            hex.setAttribute(
              'uv2', 
              new THREE.BufferAttribute(
                new Float32Array(uv2s),
                2
              )
            );
            
            cells[i].position.set(dx, height, dy);
            hexes.push(hex);
          }
          const buffer:BufferGeometry = BufferGeometryUtils.mergeBufferGeometries(hexes);
  
          buffer.computeBoundingBox();
          buffer.boundingBox?.getCenter(this.offset);
  
          buffer.center();
          buffer.translate(0,this.offset.y,0);
          //buffer.computeBoundingBox();
          resolve( buffer);
        }
      )
    });
  }

  public StartSpin = () => {
    this.animationValues.spinSpeed = 0.5;
    const camPos = this.camera!.position;
    requestAnimationFrame(this.animate);
  }
  private repositionCamera = () => {
    const props = this.animationValues;
    this.camera!.position.set(
      props.rad * Math.cos(props.rot),
      this.camera!.position.y,
      props.rad * Math.sin(props.rot)
    );
    this.camera!.lookAt(new Vector3(0,0,0));
  }
  private animate = () => {
    const props = this.animationValues;
    if (Math.abs(props.spinSpeed) > 0.0005) {
      props.rot += props.spinSpeed;
      props.spinSpeed *= 1-props.drag;

      this.repositionCamera();

      requestAnimationFrame(this.animate);
    }
    
    this.renderer!.render(this.scene!, this.camera!);
  }

  public render = () => {
    return (
      <>
      <h1 className="colour-display">{this.state.hueText}</h1>
      <div 
        ref={ref => this.mount = ref!}
        style={{
          width: '100%',
          height: '100%'
        }}
      />
      </>
    );
  }
}