import { start } from "repl";
import * as THREE from "three";

interface Vector2 {
  x:number;
  y:number;
}

export class Grid {
  private points = new Map<string, number>();

  private grid:number[][] = [];

  public setPoint = (x:number, y:number, value:number) => {
    this.points.set(`${x}x${y}`, value);
  }
  public getPoint = (x:number, y:number):number => {
    let result = this.points.get(`${x}x${y}`);
    return result !== undefined ? result : 0;
  }

  public genGrid(minX:number, minY:number, maxX:number, maxY:number) {
    this.grid = [];
    for (let x = minX; x <= maxX; x++) {
      this.grid.push([]);
      for (let y = minY; y <= maxY; y++) {
        this.grid[x].push(this.getPoint(x, y));
      }
    }
  }
  //1 = no change, 0 = complete
  public flattenGrid(flattenPerc:number, increments:number) {
    const xDim = this.grid.length;
    const yDim = this.grid[0].length;
    const eachFlatten = (1-flattenPerc) / increments;

    for (let i = 0; i < increments; i++) {
      for (let x = 0; x < xDim; x++) {
        for (let y = 0; y < yDim; y++) {
          let material = this.grid[x][y] * (eachFlatten);
          let shifted = 0;
          for (let dX = -1; dX <= 1; dX++) {
            for (let dY = -1; dY <= 1; dY++) {
              const coordX = x + dX;
              const coordY = y + dY;
              if (coordX > 0 && coordY > 0 && coordX < xDim && coordY < yDim) {
                const diff = this.grid[x][y] - this.grid[coordX][coordY];
                if (diff > 0) {
                  const inc = diff < material / 8 ? diff : material / 8;
                  this.grid[coordX][coordY] += inc;
                  shifted += inc;
                  alert(inc);
                } 
              }
            }
          }
          this.grid[x][y] -= shifted;
        }
      }
    }
  }
  public getGrid():number[][] {
    return this.grid;
  }
  public getGridPoint(x:number, y:number):number{
    return this.grid[x][y];
  }
}

interface PolygonProps {
  sides: number;
  radius: number;
  top: "edge" | "vertex";
  height: number;
}

export const genPolygon = (props:PolygonProps):THREE.BufferGeometry => {
  const geom = new THREE.BufferGeometry();
  const verts:number[] = [0,0,0];
  const tris:number[] = [];

  const stepAngle = 2 * Math.PI / props.sides;
  const startAngle = props.top == "edge" ? stepAngle / 2 : 0;
  for (let theta = startAngle; theta < 2 * Math.PI; theta += stepAngle) {
    const x = props.radius * Math.sin(theta);
    const y = -props.radius * Math.cos(theta);
    verts.push(
      x,
      0,
      y,
      x,
      -props.height,
      y,
    );
  }
  for (let i = 0; i < props.sides-1; i++) {
    tris.push(
      0,
      i*2 + 3,
      i*2 + 1,
    )
    tris.push(
      i*2 + 3,
      i*2 + 2,
      i*2 + 1,
    )
    tris.push(
      i*2 + 3,
      i*2 + 4,
      i*2 + 2,
    )
  }
  tris.push(0, 1, (props.sides-1)*2+1);
  tris.push(
    1,
    (props.sides)*2,
    (props.sides-1)*2+1,
  )
  tris.push(
    1,
    2,
    (props.sides)*2,
  )
  geom.setAttribute(
    'position', 
    new THREE.BufferAttribute(
      new Float32Array(verts),
      3
    )
  );

  geom.setIndex(tris);
  geom.computeVertexNormals();

  return geom;
}

